.login-container {
  width: 100%;
  max-width: 400px;
  margin: 0 auto;
  padding: 40px 30px;
  text-align: center;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', sans-serif;
}

.logo-container {
  text-align: center;
  margin-bottom: 24px;
}

.login-logo {
  height: 40px;
  width: auto;
  max-width: 120px;
  display: block;
  margin: 0 auto;
}

.login-title {
  text-align: center;
  color: #647484;
  font-size: 24px;
  font-weight: 500;
  margin-bottom: 32px;
}

.login-form {
  margin-bottom: 24px;
}

.form-group {
  margin-bottom: 16px;
}

.form-control {
  width: 100%;
  padding: 12px 16px;
  border: 2px solid #e1e5e9;
  border-radius: 8px;
  font-size: 16px;
  box-sizing: border-box;
  transition: border-color 0.2s;
  background-color: #f8f9fa;
}

.form-control:focus {
  outline: none;
  border-color: var(--theme-color, #684cbc);
  background-color: white;
}

.form-control::placeholder {
  color: #6c757d;
}

.form-check-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  font-size: 14px;
}

.form-check {
  display: flex;
  align-items: center;
}

.form-check-input {
  margin-right: 8px;
  width: 16px;
  height: 16px;
  accent-color: var(--theme-color, #684cbc);
}

.form-check-label {
  color: #647484;
  cursor: pointer;
  font-size: 14px;
}

.forgot-password-link {
  color: #1c74cc;
  background: none;
  border: none;
  padding: 0;
  font-size: 14px;
  cursor: pointer;
  text-decoration: none;
}

.forgot-password-link:hover {
  text-decoration: underline;
}

.btn-signin {
  width: 100%;
  padding: 12px;
  background-color: var(--theme-color, #684cbc);
  color: white;
  border: none;
  border-radius: 8px;
  font-size: 16px;
  font-weight: 500;
  cursor: pointer;
  transition: background-color 0.2s;
  margin-bottom: 16px;
}

.btn-signin:hover:not(.disabled) {
  background-color: #5a3fa8;
}

.btn-signin.disabled {
  background-color: #c5c5c5;
  cursor: not-allowed;
}

.privacy-notice {
  font-size: 12px;
  color: #666;
  line-height: 1.4;
  margin-bottom: 25px;
}

.privacy-link {
  color: #408484;
  text-decoration: none;
}

.privacy-link:hover {
  text-decoration: underline;
}

.email-code-btn {
  width: 100%;
  padding: 12px;
  background-color: transparent;
  color: var(--theme-color, #684cbc);
  border: 2px solid var(--theme-color, #684cbc);
  border-radius: 8px;
  font-size: 16px;
  font-weight: 500;
  cursor: pointer;
  margin-bottom: 24px;
  transition: all 0.2s;
}

.email-code-btn:hover {
  background-color: var(--theme-color, #684cbc);
  color: white;
}

.social-login {
  margin-bottom: 24px;
}

.btn-facebook,
.btn-apple,
.btn-google {
  width: 100%;
  padding: 12px;
  border: none;
  border-radius: 8px;
  font-size: 16px;
  font-weight: 500;
  cursor: pointer;
  margin-bottom: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  transition: opacity 0.2s;
}

.btn-facebook {
  background-color: #38569e;
  color: white;
}

.btn-apple {
  background-color: #000000;
  color: white;
}

.btn-google {
  background-color: white;
  color: #333;
  border: 1px solid #dadce0;
}

.btn-facebook:hover,
.btn-apple:hover,
.btn-google:hover {
  opacity: 0.9;
}

.social-icon {
  width: 20px;
  height: 20px;
  object-fit: contain;
}

.create-account {
  text-align: center;
  font-size: 14px;
  color: #647484;
}

.create-account-link {
  color: #1c74cc;
  background: none;
  border: none;
  padding: 0;
  font-size: inherit;
  cursor: pointer;
  text-decoration: none;
}

.create-account-link:hover {
  text-decoration: underline;
}

.alert {
  padding: 12px 16px;
  margin-bottom: 20px;
  border-radius: 4px;
  font-size: 14px;
  text-align: left;
}

.alert-danger {
  background-color: #f8d7da;
  border: 1px solid #f5c6cb;
  color: #721c24;
}

@media (max-width: 768px) {
  .login-container {
    padding: 10px;
  }

  .login-card {
    padding: 30px 20px;
  }
}
